$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary": 			#0c48ad,
				"secondary": 					#2067dc,
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      #272727,
				"brk-base-1":         #185bc9,
				"brk-base-2":         #2668d0, //h1
				"brk-base-3":         #0F5AE0,
				"brk-base-4":         #d1ddf2,
				"brk-base-5":         #00f6ff,
				"brk-base-6":         #0B98F6,
				"brk-base-7":         #a7ebff,
				"brk-base-8":         #1600ba,// TODO: !!!
				"brk-secondary-1": 		#000a1a,
				"brk-secondary-2": 		#45474c,
				"brk-secondary-3": 		#0033e0,
				"brk-secondary-4": 		#00a8e0,
				"brk-secondary-5":    #6377DF,
				"brk-secondary-6": 		#0983e0,
				"text-base-color-1":  #546887, //p
				"text-base-color-2":  #0F5AE0,
				"text-base-color-3":  #f3f3f3,
      "form-color-1":         rgba(38, 104, 208, .2)
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";
.brk-footer {
  &_no-overflow{
    overflow: hidden;
  }
  &__wrapper {
    position: relative;
    &_shadow {
      //padding-bottom: 30px;
    }
  }
  &_animated {
    position: fixed !important;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
  &__opacity-changer {
    &>.row>div {
      opacity: 0.2;
      will-change: opacity;
      transition: .3s ease opacity;
      &:first-child {
        opacity: 1;
      }
    }
    &>.row:hover>div {
      opacity: 0.2;
      &:hover {
        opacity: 1;
      }
    }
  }
  &__info-grid {
    p {
      min-width: 50%;
    }
  }
  &__rights {
    border-top: 1px solid rgba($white, 0.1);
    color: var(--brk-base-7);
  }
  &__bg-container {
    position: relative;
  }
  &__bg-left {
    position: absolute;
    left: 0;
    top: 0;
    width: 50%;
    height: 100%;
    &_footer-5 {
      width: 52%;
      clip-path: polygon(0 0, 100% 0, calc(100% - 120px) 100%, 0% 100%);
      z-index: -1;
      background-color: #1c2124;
    }
  }
  &__bg-right {
    position: absolute;
    right: 0;
    top: 0;
    width: 50%;
    height: 100%;
    &_footer-5 {
      width: 100%;
      z-index: -2;
      background-color: #161a1d;
    }
  }
  &__border-gradient {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: 3px;
      width: 100%;
      background-image: linear-gradient(
                      to left,
                      var(--brk-secondary-4) 0%,
                      var(--brk-secondary-6) 100%);
    }
  }
  &__border-gradient-1{
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: 3px;
      width: 100%;
      background-image: linear-gradient(
                      to right,
                      var(--secondary) 0%,
                      var(--brand-primary) 100%);
    }
  }
  &__rights_footer-9{
    overflow: hidden;
    .brk-footer__socials-bg{
      &:before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 1000%;
        z-index: -1;
        background-color: var(--brand-primary);
      }
      @media screen and (max-width: 768px){ 
        padding-left: 0 !important;
        padding-bottom: 15px;
        &::before{
          display: none;
        }
      }
    }
  }
  &__borders_footer-10{    
    position: relative;
    &::after{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 3px;
      width: 100%;
      background-image: linear-gradient(
                      to right,
                      var(--brand-primary),
                      var(--secondary));
    }
    &::before{
      content: "";

      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      
      background-color: rgba(256,256,256,0.16);
      z-index: 1;
    }

    .brk-footer__bordered-col{
      position: relative;
      @media screen and (min-width:991px){ 
        &:after,
        &:before{
          content: "";

          position: absolute;
          top: 0;
          
          height: 100%;
          width: 1px;

          background-color: rgba(256,256,256,0.16);
        }
        &:before{
          left: -30px;
        }
        &:after{
          right: 0px;
        }
      }
    }
  }
  &__light-bg{
    position: relative;
    z-index: 1;
    &::after{
      content: '';
      z-index: -1;
      position: absolute;
      left: -30px;
      right: -30px;      
      top: 0;
      height: 100%;
      background-color: #fff;
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);

			@media (max-width: 767px) {
				left: -15px;
				right: -15px;
			}
    }
  }
  &__wide-menu{
    position: relative;
    &::after{
      content:"";
      position: absolute;
      left: -40px;
      right: -40px;
      top: 0;
      bottom: 0;
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
      background-color: #ffffff;
      z-index: -1;

			@media (max-width: 767px) {
				left: -15px;
				right: -15px;
			}
    }
  }
  &__shop-element{
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    overflow: hidden;

    .brk-footer__shop-title{ 
      position: relative;

      &:after{
        content: "";

        position: absolute;
        left: 0;
        bottom: 0;

        width: 100%;
        height: 2px;

        background-color: rgba(256,256,256,0.2);
      }
      &_wide{
        &:after{
          @media screen and (min-width:991px){ 
            width: calc(100% + 30px);
          }
        }
      }
    }
    .brk-slider{
      position: relative;

      &__nav{
        position: absolute;
        right: 7px;
        top: -92px;
      }
      
      &__nav-prev,
      &__nav-next{
        background-color: transparent;
        cursor: pointer;
      }
      .slick-list{
        margin-left: -15px;
        margin-right: -15px;
      }
      .slick-slide{
        padding-left: 15px;
        padding-right: 15px;
      }
    }
  }
  .brk-subscribe {
    input {
      width: 100%;
      height: 54px;
    }
  }
  /*.brk-map,*/
  .brk-map__section,
  .brk-map__section>div {
    height: 100%;
    @media screen and (max-width: 1230px) {
      min-height: 520px;
    }
  }
  .brk-map__canvas{
    height: 100% !important;
  }
  .brk-map_extra {
    box-shadow: inset -3px 3px 10px rgba(0, 0, 0, 0.1);
  }
  .brk-map_extra .brk-map__canvas {
    width: 100%;
    min-height: 320px;
    height: 100% !important;
  }
  .brk-social-links_bordered {
    .brk-social-links__item {
      border: 1px solid rgba(256,256,256,.1);
      transition: .3s ease border-color;
      margin-right: 6px;
      i{
        color: rgba(256,256,256,.2);
        transition: .3s ease color;
      }
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        border-color: rgba(256,256,256,1);
        i {
          color: rgba(256,256,256,1);
        }
      }
    }
  }
  .brk-tiles-simple .before {
    background-image: linear-gradient(to right, #000000 0%, rgba(0, 0, 0, 0.6) 99%, rgba(0, 0, 0, 0.6) 100%);
  }
  .brk-tiles-simple{
    min-height: 270px;
  }
  &-map-overflow{
    position: relative;
    .brk-map{
      width: calc(100% - 540px);
      height: 420px;
      @media screen and (max-width: 1230px) {
        width: 100%;
        height: 350px;
        min-height: 0;
      }
    }
    &__info{
      position: absolute;
      right: 0;
      top: 0;

      height: 100%;

      padding-top: 76px;
      padding-left: 170px;
      padding-right: 65px;

      background-image: linear-gradient(to right, var(--brand-primary) 0%, var(--secondary) 100%);
      clip-path: polygon(150px 0%, 100% 0, 100% 100%, 0% 100%);

      @media screen and (max-width: 1230px){ 
        position: relative;
        clip-path: none;
        padding: 20px 15px;
        text-align: center;
      }
    }
  }
  
}

