.brk-angle-list{
  &__item{
    position: relative;

    i{
      position: relative;
      left: -5px;

      display: inline-block;

      margin-top: -2px;
      padding-right: 0;
      width: 0;

      overflow: hidden;

      transition: .3s ease width, .3s ease padding-right, .3s ease left;
      will-change: transition, padding-right, left;
      vertical-align: middle;
    }
    &:hover{
      color: #000000;

      i{
        left: 0;

        padding-right: 10px;
        width: 10px;
      }
    }
  }
}

